.form-wrap {
  margin: 30px 0;
}

.form-wrap form {
  display: flex;
  gap: 20px;
}

.form-wrap form input {
  flex-basis: 70%;
  line-height: 30px;
  padding: 0 15px;
  font-size: 16px;
}

.form-wrap form input:focus {
  outline: none;
}

.form-wrap form button {
  flex-basis: 30%;
}
